 qu’il est lui aussi constitué de bâtons horizontaux et verticaux. Tu te demandes alors mais quelle est la différence entre les 2 ? La réponse est simple, le code « chinois » et le code samouraï fonctionnent de la même façon sauf que, dans le code samouraï les voyelles sont représentées par les lignes verticales et les consonnes par les lignes horizontales (c’est l’inverse du code « chinois » !). ")]),_c('img',{staticClass:"img-center",attrs:{"src":require("@/activities/3/10/img/10.png")}})])]),_c('div',{attrs:{"id":"page6"}},[_c('div',{staticClass:"blockquote"},[_c('h1',[_vm._v("Etape 3 : A toi de jouer !")]),_c('p',[_vm._v(" Ta mission, ici, est de décoder le code « samourai » ci-dessous (voyelles en lignes verticales et consonnes en lignes horizontales). Une fois que tu auras le texte en langage clair, code-le en code des templiers. Ensuite envoie une photo de ton code des templiers, qu’on puisse le vérifier et te valider l’activité. ")]),_c('img',{staticClass:"img-center",attrs:{"src":require("@/activities/3/10/img/11.png")}}),_c('br'),_c('p',[_vm._v(" Pour finir, charge ton résultat sur le site. Il sera validé au plus vite par des chefs et cheftaines ! ")])])])])}]

